import { Component, Input, TemplateRef} from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-foundation/modal';
import { LoginFormComponent } from './login-form.component';
import { RegisterFormComponent } from './register-form.component';


@Component({
  selector: 'app-login-modal',
  templateUrl: './login-modal.component.html',
  styleUrls: ['./login-modal.component.scss']
})
export class LoginModalComponent {
  isVisible: boolean;
  bsModalRef: BsModalRef;
  registerForm: RegisterFormComponent;
  template: TemplateRef<any>;

  @Input() loginForm: LoginFormComponent;


  constructor(private modalService: BsModalService) {
  }

  private showModal(template: TemplateRef<any>){   
    this.isVisible = true;
    this.bsModalRef = this.modalService.show(template, {});
  }

  showLoginForm(template: TemplateRef<any>){
    this.showModal(template);
  }

}
